import React from "react";
import { Helmet } from "react-helmet";
import { Company } from "../interfaces";
import { useTranslation } from "gatsby-plugin-react-i18next";

interface Props {
    data: Company;
}

const SEO:React.FC<Props> = ({ data }) => {
  const { t } = useTranslation();
  const schemaMarkup = {
    "@context": "https://schema.org",
    "@type": "LocalBusiness",
    "name": "Code Crafted Studios",
    "image": "https://instagram.fcyw7-1.fna.fbcdn.net/v/t51.2885-19/461879342_838381215119293_2054415860225868366_n.jpg?stp=dst-jpg_tt6&_nc_ht=instagram.fcyw7-1.fna.fbcdn.net&_nc_cat=101&_nc_ohc=rKPGCj7tqrIQ7kNvgH2opHh&_nc_gid=b397b45fa75b4bec80460244f1c57a96&edm=AP4sbd4BAAAA&ccb=7-5&oh=00_AYCjvJ1QWm8Hp60-ntOR-YzZTlE-_b_q27yWZhVTC2LyzQ&oe=6753E7F7&_nc_sid=7a9f4b",
    "address": {
      "@type": "PostalAddress",
      "streetAddress": data.address,
      "addressLocality": data.city,
      "addressRegion": data.province,
      "postalCode": data.postalCode,
      "addressCountry": "CA"
    },
    "geo": {
      "@type": "GeoCoordinates",
      "latitude": 45.35475,
      "longitude": -73.7376551
    },
    "url": data.url,
    "telephone": data.phone,
    "priceRange": "$$",
    "openingHours": "Mo-Fr 09:00-17:00"
  };

  return (
    <Helmet>
      <meta name="description" content={t?.("keywords.pymes") ?? ""} />
      <title>{t?.("keywords.general") ?? ""}</title>
      <script type="application/ld+json">
        {JSON.stringify(schemaMarkup)}
      </script>
    </Helmet>
  );
};

export default SEO;
