import React, { useEffect, useState } from "react";
import { LayoutProps } from "../interfaces";
import Navigation from "../components/nav";
import LanguageSelector from "../components/languageSelector";
import MainLogo from "../components/mainLogo";
import CookieConsent from "react-cookie-consent";
import { useTranslation } from "gatsby-plugin-react-i18next";
import SEO from "../components/seo";
import { Company } from "../interfaces";
import data from "../data/data.json";
import { BiLogoInstagramAlt } from "react-icons/bi";
import {
  FaSquareFacebook,
  FaSquareWhatsapp,
  FaSquarePhone,
  FaLinkedin,
} from "react-icons/fa6";

const info: Company = data[0];

const Layout: React.FC<LayoutProps> = ({ children, data }) => {
  const { t } = useTranslation();
  const [cookiesAccepted, setCookiesAccepted] = useState(false);

  useEffect(() => {
    if (cookiesAccepted) {
      enableGoogleAnalytics();
    } else {
      disableGoogleAnalytics();
    }
  }, [cookiesAccepted]);

  const enableGoogleAnalytics = () => {
    if (typeof window !== "undefined") {
      const gtag = (window as any).gtag;
      if (gtag) {
        gtag("consent", "update", {
          analytics_storage: "granted",
        });
      }
    }
  };

  const disableGoogleAnalytics = () => {
    if (typeof window !== "undefined") {
      const gtag = (window as any).gtag;
      if (gtag) {
        gtag("consent", "update", {
          analytics_storage: "denied",
        });
      }
    }
  };

  function formatPhoneNumber(phoneNumber: string) {
    const cleaned = ("" + phoneNumber).replace(/\D/g, "");
    const match = cleaned.match(/^(?:\+1)?(\d{11})$/);
    if (match) {
      const digits = match[1];
      return `+1 (${digits.slice(1, 4)}) ${digits.slice(4, 7)}-${digits.slice(
        7,
        11
      )}`;
    }
    return phoneNumber;
  }

  const currentYear = new Date().getFullYear();

  return (
    <>
      {data && (
        <>
          <SEO data={data} />
          <div className={styles.wrapper}>
            <div className={styles.header}>
              <div className={styles.logoContainer}>
                <MainLogo fillColour="#FCFCFC" />
              </div>
              <div className={styles.navbar}>
                <Navigation data={data} />
              </div>
              <div>
                <LanguageSelector />
              </div>
            </div>
            <div className={styles.childrenContent}>
              <main>{children}</main>
            </div>
          </div>
          <CookieConsent
            location="bottom"
            buttonText={t("granted")}
            declineButtonText={t("denied")}
            enableDeclineButton
            cookieName="gatsby-google-analytics-consent"
            expires={150}
            onAccept={() => setCookiesAccepted(true)}
            onDecline={() => setCookiesAccepted(false)}
            overlay={true}
            buttonClasses="!border !px-6 !py-2 !rounded-full !bg-light"
            declineButtonClasses="!border !px-6 !py-2 !rounded-full !bg-dark"
            overlayClasses="!bg-dark/90"
            containerClasses="bg-dark border-2 border-light rounded-2xl gradient p-2 sm:p-4 poppins-regular"
            contentClasses="text-white"
          >
            {t("cookieConsent")}
          </CookieConsent>
          <footer
            className="flex flex-col sm:flex-row bg-dark p-2 sm:p-0"
            id={info?.menu[3]}
          >
            <div className="flex w-full sm:w-1/3 h-12 sm:h-32 px-32 py-8">
              <MainLogo fillColour="#FCFCFC" />
            </div>
            <div className="flex flex-col w-full sm:w-1/3 text-light text-center self-center">
              <div className="w-full">
                {info.address}, {info.city}, {info.province}, {info.country}
              </div>
              <div className="w-full">
                <a
                  href={`tel:${info.phone}`}
                  className="flex flex-row gap-2 justify-center items-center text-lg font-bold"
                >
                  <FaSquarePhone />
                  {formatPhoneNumber(info.phone)}
                </a>
              </div>
              <div className="w-full">
                &copy; {currentYear} {info.companyName}
              </div>
            </div>
            <div className="flex flex-row w-24 sm:w-1/3 text-light self-center justify-center -order-2 sm:order-1">
              <a
                href={info.instagram}
                className={styles.social}
                target="_blank"
              >
                <BiLogoInstagramAlt className="w-12 h-12" />
                <span className="text-xs">Instagram</span>
              </a>
              <a href={info.facebook} className={styles.social} target="_blank">
                <FaSquareFacebook className="w-12 h-12" />
                <span className="text-xs">Facebook</span>
              </a>
              <a href={info.whatsapp} className={styles.social} target="_blank">
                <FaSquareWhatsapp className="w-12 h-12" />
                <span className="text-xs">Whatsapp</span>
              </a>
              <a href={info.linkedin} className={styles.social} target="_blank">
                <FaLinkedin className="w-12 h-12" />
                <span className="text-xs">Linkedin</span>
              </a>
            </div>
          </footer>
        </>
      )}
    </>
  );
};

export default Layout;

const styles = {
  wrapper: "flex flex-col min-h-screen",
  header: "grid grid-flow-col justify-stretch items-center",
  logoContainer: "flex h-32 sm:h-60 w-32 sm:w-60 mx-auto pt-8",
  navbar: "flex w-1/2",
  childrenContent: "text-light p-2 sm:p-8 mt-8",
  social: "flex flex-col items-center justify-center mx-4",
};
